import { ref } from 'vue';
import { useStore } from 'vuex';
import { useRoute, useRouter } from 'vue-router'; // component

import PespeakHeader from './cpns/pespeakHeader.vue';
import WeekInfo from './cpns/weekInfo.vue';
import PespeakForm from './cpns/pespeakForm.vue';
export default {
  components: {
    PespeakHeader: PespeakHeader,
    WeekInfo: WeekInfo,
    PespeakForm: PespeakForm
  },
  setup: function setup() {
    var store = useStore();
    var route = useRoute();
    var router = useRouter(); // 判斷url是否帶有query

    if (route.query.stamp === undefined || route.query.stamp === null) router.push('/main');
    if (store.state.currentTimeStamp === '') store.commit('setCurrentTimeStamp', route.query.stamp);
    store.commit('setCurrentStamp', route.query.stamp);
    var typeArr = ['日檢視', '週檢視'];
    var selectList = ref([]);
    var isShowSelectBox = ref(false);
    var selectBoxIndex = ref(0);

    var emitDateType = function emitDateType(type) {
      if (type === 'date') {
        selectList.value = typeArr;
        isShowSelectBox.value = !isShowSelectBox.value;
      }
    };

    var chooseSelectItem = function chooseSelectItem(index) {
      selectBoxIndex.value = index;
      isShowSelectBox.value = !isShowSelectBox.value;
    };

    var clickMask = function clickMask() {
      isShowSelectBox.value = !isShowSelectBox.value;
    };

    return {
      typeArr: typeArr,
      isShowSelectBox: isShowSelectBox,
      selectBoxIndex: selectBoxIndex,
      emitDateType: emitDateType,
      clickMask: clickMask,
      chooseSelectItem: chooseSelectItem
    };
  }
};