import dayjs from 'dayjs'; // 計算當週date

export function currentWeekFn(stamp) {
  var weekDate = ['S', 'M', 'T', 'W', 'T', 'F', 'S'];
  var currentWeekArr = [];
  var startDate = dayjs(stamp).startOf('week').add(0, 'day').valueOf();

  for (var i = 0; i < 7; i++) {
    // 每日時段(30min)
    var pespeakInfo = [];
    var aDateStamp = dayjs(startDate).valueOf();

    for (var _i = 0; _i < 48; _i++) {
      pespeakInfo.push({
        time: dayjs(aDateStamp + _i * 1800000).format('hh:mma'),
        content: ''
      });
    } // week


    currentWeekArr.push({
      date: dayjs(startDate + 86400000 * i).format('DD'),
      weekDate: weekDate[i],
      fullDate: dayjs(startDate + 86400000 * i).format('YYYY/M/D'),
      pespeakInfo: pespeakInfo
    });
  }

  return currentWeekArr;
} // export function halfHourFn() {
